.acc-item{
  background-color: aquamarine;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
  

  /* Bos shadow */
  box-shadow: 13px 12px 9px -7px rgba(196,185,185,1);
  -webkit-box-shadow: 13px 12px 9px -7px rgba(196,185,185,1);
  -moz-box-shadow: 13px 12px 9px -7px rgba(196,185,185,1);
}

.acc-item:hover{
  border-left: 5px solid purple;
  transition-duration: 1s;
}

.acc-title{
  display: flex;
  justify-content: space-between;
}

.acc-opened{
  border-left: 5px solid orange;
  transition-duration: 1s;
  background-color: white;
}